import {useEffect, lazy, Suspense, useState} from 'react';
import { Route, Switch, useRouteMatch, useHistory, Redirect } from 'react-router-dom';
import {useDispatch, useSelector} from 'react-redux';
import {LoadUser} from '../application/middlewares/auth';
import {getIsLogged} from '../application/selectors/auth';
import { post, fetchData } from '../application/middlewares/index';
import {Link} from 'react-router-dom'
import Button from "@material-tailwind/react/Button";
import image from './image/gamysh.jpg';
import image1 from './image/hotel.jpg'
import image2 from './image/hotel1.jpg'
import image3 from './image/hotel2.jpeg'
import { Icon } from 'react-icons-kit'
import {smallDown} from 'react-icons-kit/entypo/smallDown'
import {smallUp} from 'react-icons-kit/entypo/smallUp'
import { getCategory } from '../application/selectors/auth';


function Search(){

    const categories = useSelector(getCategory);
    // console.log(categories);

    const dispatch = useDispatch();

    const [locations, setLocations] = useState();

    useEffect(() => {
        dispatch(fetchData({
            url:'tm/main-locations',
            token:'',
            action:(response)=>{
                if(response.success){
                    // console.log("get_location ",response)
                    // console.log(response.data.rows[0].real_estates_all[0])
                    setLocations(response.data.rows);
                    // console.log(products)
                } else {
                    console.log("error_data ",response);
                    }
                }
            }))
    }, []);
    var selects = document.querySelectorAll(".labels");
        selects.forEach(function(item) {
        if(item.children[0].checked) {
        let q= item.classList.add("labels_active");
        } else {
        let q= item.classList.remove("labels_active");
        }
    });

    const [isActive1, setActive1] = useState("false");
    const [isActive2, setActive2] = useState("false");
    const [isActive3, setActive3] = useState("false");
    const [isActive4, setActive4] = useState("false");

    const [category_id, setCategory_id] = useState(1);
    const [category_name, setCategory_name] = useState(['Kwartira']);
    const [type_id, setType_id] = useState(3);
    const [areaMin, setAreaMin] = useState(null);
    const [areaMax, setAreaMax] = useState(null);
    const [priceMin, setPriceMin] = useState(null);
    const [priceMax, setPriceMax] = useState(null);
    const [location_id, setLocation_id] = useState();
    const [location_name, setLocation_name] = useState('Locations...');

    const history = useHistory()
    const SearchFunc = () =>{
        const price = {"min":priceMin, "max":priceMax};
        let priceStr = JSON.stringify(price);
        const area = {"min":areaMin, "max":areaMax};
        let areaStr = JSON.stringify(area);
        history.push(`/products?category_id=${category_id ? `${category_id}`: ``}&type_id=${type_id ? `${type_id}` : ``}&location_id=${location_id ? `${location_id}` : ``}&price=${priceStr}`)
    }
    const ToggleClass1 = () => {
        setActive1(!isActive1); 
       };
    const ToggleClass2 = () => {
        setActive2(!isActive2); 
       };
    const ToggleClass3 = () => {
        setActive3(!isActive3); 
       };
    const ToggleClass4 = () => {
        setActive4(!isActive4); 
       };
    return(
        <main>
            <div className="back_img" style={{ backgroundImage: `url(${image})` }}>
                <div className="image_gradient">
                <div className="header_text">
                    <h1 className="text-4xl font-medium text-white">#лучшедома ищи на gamysh</h1>
                    <div className="select1">
                        {categories?.map((item)=>(
                            <label key={item.id} onClick = {(e)=>{
                                setCategory_id(item.id);
                                var selects = document.querySelectorAll(".labels");
                                selects.forEach(function(item) {
                                    if(item.children[0].checked) {
                                        let q= item.classList.add("labels_active");
                                        } else {
                                            let q= item.classList.remove("labels_active");
                                        }
                                  });
                                // console.log(item.id," ",category_id);
                            }} className="labels flex items-center " >
                                {item.id==1 ?
                            <input type='radio' name="rr" id="lab" checked className="hidden" />
                            :
                            <input type='radio' name="rr" id="lab" className="hidden" />
                        }
                            <p>{item.name}</p>
                            </label>
                        ))}
                    </div>
                    <div className="select2">
                        <div className="sel_home hover:bg-blue-100 rounded-sm" >
                            <div className="onclick1 px-2 flex justify-between" onClick={ToggleClass1}>
                                <p className="flex">
                                    <p>
                                    {category_name}
                                    </p>
                                </p>
                            <Icon size={20} icon={smallDown} className="text-gray-500"/>
                            </div>
                        <div className={isActive1 ? "hidden" : "dropdown1 shadow-lg border py-2"}>
                            {categories[category_id-1]?.main_types?.map((item_main)=>(
                                    <div className="drop1">
                                    <h3 className="font-medium text-base px-2" key={item_main.id}>{item_main.name}</h3>
                                    {item_main?.sub_types?.map((item_sub)=>(
                                       
                                    <label onClick = {()=>{
                                        setType_id(item_sub.id);
                                        setCategory_name(item_sub.name)
                                    }}
                                    className="flex items-center text-sm cursor-pointer p-2 hover:bg-gray-100">
                                       <input type="radio" name="sub" className="mr-4" />
                                       <p key={item_sub.id} >{item_sub.name}</p>
                                    </label> 
                                    ))}
                                </div> 
                            ))}
                            
                        </div>
                        </div>
                        <div className="sel_qty hover:bg-blue-100 rounded-sm" >
                            <div className="onclick1 px-2 flex justify-between" onClick={ToggleClass2}>
                                <p className="flex">
                                    <p>Area</p>
                                </p>
                            <Icon size={20} icon={smallDown} className="text-gray-500"/>
                            </div>
                        <div className={isActive2 ? "hidden" : "dropdown1 shadow-lg border"}>
                        <div className="inputs p-4">
                                    <input placeholder="от" className="p-3 focus:outline-none focus:ring-2 focus:ring-blue-600" 
                                    onChange = {(e)=>setAreaMin(e.target.value)}/>
                                    <input placeholder="до" className="p-3 focus:outline-none focus:ring-2 focus:ring-blue-600" 
                                    onChange = {(e)=>setAreaMax(e.target.value)}/>
                        </div>
                            {/* <div className="btn_group p-4">
                                <p>1</p>
                                <p>2</p>
                                <p>3</p> 
                                <p>4</p>
                                <p>5</p>
                                <p>6+</p>
                            </div>
                            <p className="flex items-center text-sm cursor-pointer p-2 hover:bg-gray-100">
                                    <input type="checkbox" className="mr-4" />
                                    <p>Снять</p>
                            </p>
                            <p className="flex items-center text-sm cursor-pointer  p-2 hover:bg-gray-100">
                                    <input type="checkbox" className="mr-4" />
                                    <p>Lorem</p>
                            </p> */}
                        </div>
                        </div>
                        <div className="sel_price hover:bg-blue-100 rounded-sm">
                        <div className="onclick1 px-2 flex justify-between" onClick={ToggleClass3}>
                                <div className="flex">
                                    <p>Price</p>
                                </div>
                            <Icon size={20} icon={smallDown} className="text-gray-500"/>
                            </div>
                        <div className={isActive3 ? "hidden" : "dropdown1 p-4 shadow-lg border"}>
                        <div className="inputs">
                                    <input placeholder="от" className="p-3 focus:outline-none focus:ring-2 focus:ring-blue-600" 
                                    onChange = {(e)=>setPriceMin(e.target.value)}/>
                                    <input placeholder="до" className="p-3 focus:outline-none focus:ring-2 focus:ring-blue-600" 
                                    onChange = {(e)=>setPriceMax(e.target.value)}/>
                        </div>
                        </div>
                        </div>
                        <div className="sel_location hover:bg-blue-100 rounded-sm">
                        <div className="onclick1 px-2" onClick={ToggleClass4}>
                            <div className="flex">
                                    <p>{location_name}</p>
                            </div>
                        </div>
                            <div className={isActive4 ? "hidden" : "dropdown_city shadow-lg border"}>
                            {locations?.map((location)=>(
                                <label  onClick={()=>{
                                    setLocation_id(location.id);
                                    setLocation_name(location.name);
                                }}
                                className="flex items-center text-sm  p-2 hover:bg-gray-100">
                                    <input type="radio" name="loc" className="mr-3" />
                                 <p>{location.name}</p>
                                {/* <Icon size={18} icon={ic_check} className="text-blue-600"/> */}
                            </label>
                                ))}
                            </div>
                        </div>
                    </div>
                    <div className="flex justify-end mt-4">
                        <button className="btn3 mr-4 font-medium">See map</button>
                        <button onClick = {()=>SearchFunc()} className="btn1 mr-0 font-medium">Search</button>
                    </div>
                </div>
                </div>
            </div>
        </main>

    )
}

export default Search;